  const dateFormat = (dateString) => {
    const formattedString = dateString.replace(/(\d{4})-(\d{2})-(\d{2})/, "$1년 $2월 $3일");
    return formattedString;
  };

  //date를 YYYY.MM.DD로 변환
  function formatDate(inputDate) {
    // 입력된 날짜 문자열을 '-'로 분리하여 배열로 만듭니다.
    const dateParts = inputDate.split("-");

    // 연도, 월, 일을 추출
    const year = dateParts[0];
    const month = dateParts[1];
    const day = dateParts[2];

    // 새로운 형식으로 날짜 문자열 생성
    const formattedDate = `${year}.${month}.${day}`;
    const NewformattedDate = formattedDate.split("T")[0];
    return NewformattedDate;
  }
  const dateFormat2 = (date) => {
    const orginalDate = new Date(date);

    return orginalDate;
  }

  function groupDataByDate(data) {
    const groupedData = {};
    data?.forEach((item) => {
      const date = item.date;

      if (!groupedData[date]) {
        groupedData[date] = [];
      }

      groupedData[date].push(item);
    });
    // console.log(groupedData);
    setDateList(Object.keys(groupedData));
    setDateGroupData(groupedData);
  }


  function deg2rad(deg) {
    return deg * (Math.PI / 180);
  }
  function getDistance(lat1, lon1, lat2, lon2) {
    if (lat1 && lon1 && lat2 && lon2) {
      const R = 6371; // 지구의 반지름 (단위: 킬로미터)
      const dLat = deg2rad(lat2 - lat1);
      const dLon = deg2rad(lon2 - lon1);

      const a =
        Math.sin(dLat / 2) * Math.sin(dLat / 2) +
        Math.cos(deg2rad(lat1)) * Math.cos(deg2rad(lat2)) * Math.sin(dLon / 2) * Math.sin(dLon / 2);

      const c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a));
      const distance = R * c * 1000; // 거리 (미터)
      // totalDistance.current += distance;
      return distance;
    }
    return 0;
  }

